<template>
  <!-- 登录 -->
  <v-app v-if="$global.flag1 == 0 && flag2 == 0">
    <template>
      <v-app-bar app color="white" height="70">
        <v-avatar class="mr-3" color="grey lighten-5" size="70">
          <v-img contain max-height="70%" src="https://tva1.sinaimg.cn/large/0081Kckwly1gk1wb2jnilj308c08cwf0.jpg"></v-img>
        </v-avatar>
        <v-toolbar-title class="font-weight-black headline">
          GAODI
        </v-toolbar-title>
      </v-app-bar>
      <v-img max-height="729px"
        src="https://images.unsplash.com/photo-1487017159836-4e23ece2e4cf?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1951&q=80">
        <v-container id="sign" class="fill-height" fluid>
          <v-row align="center" justify="center" class="white--text mx-auto">
            <v-col cols="12" md="7" class="white--text text-center" tag="h1">
              <div class="font-weight-light" style="font-size: 64px;">
                WELCOME TO
              </div>
              <div class="font-weight-black" style="font-size: 82px;">
                GAODI
              </div>
            </v-col>
            <v-col cols="12" md="5">
              <v-card class="elevation-12" style="margin-right: 70px;" width="450px">
                <v-toolbar color="#5D4037" dark flat>
                  <v-toolbar-title>登录</v-toolbar-title>
                  <v-spacer></v-spacer>
                </v-toolbar>
                <v-card-text>
                  <v-form>
                    <v-text-field label="账号" v-model="login.account" name="account1" prepend-icon="mdi-account"
                      type="text" color="#5D4037">
                    </v-text-field>
                    <v-text-field label="密码" v-model="login.password" name="password1" prepend-icon="mdi-lock"
                      type="password" color="#5D4037"></v-text-field>
                  </v-form>
                  <v-radio-group v-model="login.radio" mandatory row>
                    <v-radio label="学生" value="0" color="#5D4037"></v-radio>
                    <v-radio label="教师" value="1" color="#5D4037"></v-radio>
                    <v-radio label="管理员" value="2" color="#5D4037"></v-radio>
                  </v-radio-group>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="#5D4037" @click="click2">注册</v-btn>
                  <v-btn color="#5D4037" @click="click1">登录</v-btn>
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-img>
    </template>
  </v-app>

  <!-- 登录后 -->
  <v-app v-else-if="$global.flag1 == 1 && flag2 == 0">
    <dashboard-core-app-bar />

    <dashboard-core-drawer />

    <dashboard-core-view />

    <dashboard-core-settings />
  </v-app>

  <!-- 注册 -->
  <v-app v-else-if="flag2 == 1">
    <template>
      <v-app-bar app color="white" height="70">
        <v-avatar class="mr-3" color="grey lighten-5" size="70">
          <v-img contain max-height="70%" src="https://tva1.sinaimg.cn/large/0081Kckwly1gk1wb2jnilj308c08cwf0.jpg"></v-img>
        </v-avatar>
        <v-toolbar-title class="font-weight-black headline">
          GAODI
        </v-toolbar-title>
      </v-app-bar>
      <v-img max-height="729px"
        src="https://images.unsplash.com/photo-1487017159836-4e23ece2e4cf?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1951&q=80">
        <v-container id="sign" class="fill-height" fluid>
          <v-row align="center" justify="center" class="white--text mx-auto">
            <v-col cols="12" md="7" class="white--text text-center" tag="h1">
              <div class="font-weight-light" style="font-size: 64px;">
                WELCOME TO
              </div>
              <div class="font-weight-black" style="font-size: 82px;">
                GAODI
              </div>
            </v-col>
            <v-col cols="12" md="5">
              <v-card class="elevation-12" style="margin-right: 70px;" width="450px">
                <v-toolbar color="#5D4037" dark flat>
                  <v-toolbar-title>注册</v-toolbar-title>
                  <v-spacer></v-spacer>
                </v-toolbar>
                <v-card-text>
                  <v-form>
                    <v-text-field label="账号" v-model="register.account" prepend-icon="mdi-account" type="text"
                      color="#5D4037">
                    </v-text-field>
                    <v-text-field @blur="check_pwd" v-model="register.password" label="密码" prepend-icon="mdi-lock"
                      type="password" color="#5D4037"></v-text-field>
                    <v-alert v-show="rules" type="error" dismissible>
                      密码最少为6位
                    </v-alert>
                    <v-text-field @blur="check_cpwd" v-model="register.password2" label="确认密码" prepend-icon="mdi-lock"
                      type="password" color="#5D4037"></v-text-field>
                    <v-alert v-show="rules2" type="error" dismissible>
                      两次输入的密码不一致
                    </v-alert>
                    <v-text-field label="姓名" v-model="register.name" prepend-icon="mdi-account" type="text"
                      color="#5D4037">
                    </v-text-field>
                    <v-text-field label="学校" v-model="register.school_name" prepend-icon="mdi-account" type="text"
                      color="#5D4037">
                    </v-text-field>
                    <v-text-field label="学/工号" v-model="register.number" prepend-icon="mdi-account" type="text"
                      color="#5D4037">
                    </v-text-field>
                  </v-form>
                  <v-radio-group v-model="register.radio" mandatory row>
                    <v-radio label="学生" value="0" color="#5D4037"></v-radio>
                    <v-radio label="教师" value="1" color="#5D4037"></v-radio>
                    <v-radio label="管理员" value="2" color="#5D4037"></v-radio>
                  </v-radio-group>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="#5D4037" @click="click3">返回</v-btn>
                  <v-btn color="#5D4037" @click="click4">注册</v-btn>
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-img>
    </template>
  </v-app>

</template>

<script>
  export default {
    name: 'DashboardIndex',
    components: {
      DashboardCoreAppBar: () => import('./components/core/AppBar'),
      DashboardCoreDrawer: () => import('./components/core/Drawer'),
      DashboardCoreSettings: () => import('./components/core/Settings'),
      DashboardCoreView: () => import('./components/core/View'),
    },

    data() {
      return {
        expandOnHover: false,
        flag2: 0,
        rules: false,
        rules2: false,
        userMsg: "",
        login: {
          account: '',
          password: '',
          radio: 0,
        },
        register: {
          account: '',
          password: '',
          password2: '',
          name: '',
          school_name: '',
          number: '',
          radio: 0,
        },
      }
    },

    methods: {
      // 登录
      click1: function () {
        var that = this;
        if (this.login.account === '' || this.login.password === '') {
          alert('账号或密码不能为空！');
        } else {
          this.$axios({
              method: 'get',
              url: '/api/log_in/',
              params: {
                account: that.login.account,
                password: that.login.password,
                identity: that.login.radio,
              }
            })
            .then(function (response) {
              console.log("登录：", response.data)
              if (response.data.msg == '登录成功') {
                that.$global.userMsg.id = response.data.id;
                that.$global.userMsg.name = response.data.name;
                that.$global.userMsg.number = response.data.number;
                that.$global.userMsg.school_name = response.data.school_name;
                that.$global.userMsg.school_id = response.data.school_id;
                that.$global.userMsg.classes = response.data.classes;
                that.$global.userMsg.account = response.data.account;
                that.$global.userMsg.identity = that.login.radio;
                that.$global.userMsg.avatar = response.data.avatar;
                that.$global.flag1 = 1;
                that.$forceUpdate();
              } else if (response.data.msg == '密码不正确') {
                alert("密码不正确！")
              } else if (response.data.msg == '账号不存在') {
                alert("账号不存在！")
              }
            })
        }
      },
      // 跳转到注册
      click2: function () {
        this.$global.flag1 = 2;
        this.flag2 = 1;
        this.$forceUpdate();
      },
      click3: function () {
        this.$global.flag1 = 0;
        this.flag2 = 0;
        this.$forceUpdate();
      },
      check_pwd: function () {
        if (this.register.password.length < 6) {
          this.rules = true;
        }
      },
      check_cpwd: function () {
        if (this.register.password != this.register.password2) {
          this.rules2 = true;
        }
      },
      /* 注册函数
        1、检查该用户是否注册
        2、若已注册则提示已注册
        3、若未注册，则传数据到django，并在数据库增加用户（注册可以增加填写激活码以商用）
        4、注册成功后提示并返回登录界面
      */
      click4: function () {
        var that = this;
        if (this.register.account === '' || this.register.password === '') {
          alert('账号或密码不能为空！');
        } else if (this.register.password.length < 6) {
          alert('密码最少为6位！');
        } else if (this.register.password2 != this.register.password) {
          alert('两次输入的密码不一致！');
        } else {
          this.$axios({
              method: 'get',
              url: '/api/register/',
              params: {
                account: that.register.account,
                password: that.register.password,
                name: that.register.name,
                school_name: that.register.school_name,
                number: that.register.number,
                identity: that.register.radio,
              },
            })
            .then(function (response) {
              if (response.data.msg == '注册成功') {
                that.$global.flag1 = 0;
                that.flag2 = 0;
                that.$forceUpdate();
                alert("注册成功！");
              } else {
                alert("账号已存在或学校名称有误！")
              }
            })
        }
      },
    },
  }
</script>